.form_checkbox .input {
  margin: 0 0 22px 0;
}
.form_checkbox .input .input-lbl {
  font-size: 17px;
  font-weight: 500;
  margin: 0 0 10px 0;
  color: #62635d;
}
.form_checkbox .input input {
  width: 37%;
  height: 40px;
  border-radius: 20px;
  background: #fb9500;
  border: 2px solid #fb9500;
  outline: none;
  padding: 0 40px;
  font-weight: 600;
  color: white;
}
.form_checkbox .input .DisableClass {
  background: white !important;
  color: #62635d;
  border: 2px solid #62635d !important;
  position: relative;
}
.form_checkbox .input .anableClass {
  background: #fb9500 !important;
  border: 2px solid #fb9500 !important;
  position: relative;
}
.iconColor {
  color: #62635d;
}
.form_checkbox .input .missingtext {
  background: white !important;
  border: 2px solid red !important;
  position: relative;
  color: #62635d;
}
.form_checkbox .input input::placeholder {
  color: white;
}
.form_checkbox .checkbox {
  margin-left: 100px;
  font-size: 17px;
  font-weight: 550;
  color: #62635d;
}

.ant-checkbox .ant-checkbox-inner {
  background-color: #62635d !important;
  border: 0 !important;
}

.ant-checkbox-checked .ant-checkbox-inner {
  background: #fb9500 !important;
  border: 0 !important;
}
.ant-checkbox .ant-checkbox-inner:hover {
  border: 0 !important;
}
.EruoSymbleDisableClass {
  position: absolute;
  top: 45px;
  left: 14px;
  color: #62635d;
  font-size: 14px;
  font-weight: 500;
  z-index: 300;
  box-sizing: border-box;
}

.EruoSymbleanableClass {
  position: absolute;
  top: 45px;
  font-weight: 500;
  left: 14px;
  z-index: 300;

  color: white;
  font-size: 14px;
  box-sizing: border-box;
}

.form_checkbox {
  position: relative;
}
